import {
	getSecurity,
} from '@/api/job'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
export default {
	data() {
		return {
			detail: null,
		}
	},
	onShow() {
		uni.hideKeyboard()
		this.getSecurity()
	},
	methods: {
		updateTo(i) {
			if (i === 1) {
				uni.navigateTo({
					url: '/pages/my/update?type=' + i +'&phone=' + this.detail.phone
				});
			} else {
				uni.navigateTo({
					url: '/pages/my/update?type=' + i
				});
			}
		},
		FedLogOut() {
			this.$store.dispatch('FedLogOut').then(res => {
				$wuxToast().show({
					type: 'success',
					text: '已退出登陆'
				})
				setTimeout(() => {
					uni.switchTab({
						url: '/pages/index/index'
					});
				}, 1000)
			})
		},
		getSecurity() {
			getSecurity().then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.detail = r.detail
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
					setTimeout(() => {
						uni.navigateBack();
					}, 1500)
				}
			})
		}
	}
}
